<template>
  <div>
    <h2>Out-of-Context Hillwalkers' Quotes</h2>
    <P>
      It is an axiom of ours (mine, at least) that the funniest things are said accidentally. We collected some of the funniest words ever said by our club members and they are here for your enjoyment:
    </P>

    <v-card dark style="min-height:200px">
      <v-card-text v-if="this.loadError" style="color:red">
        Error!
      </v-card-text>
      <v-card-text v-else-if="this.current >= 0">
        <span v-for="line in this.quotes[this.current]">{{ line }}<br></span>
      </v-card-text>

      <v-card-actions>
        <v-btn :disabled="this.loadError"
               :loading="this.loading"
               @click="nextQuote()">
          Show me{{ blankOrMore }}!
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import axios from 'axios';

function swap (arr, i, j) {
  let temp = arr[i];
  arr[i] = arr[j];
  arr[j] = temp;
}

function shuffleBatches (data) {
  for (let i=0; i < data.length; i++)
    for (let j=0; j < data[i].length; j++)
      swap (data[i],
        Math.floor(Math.random() * data[i].length),
        Math.floor(Math.random() * data[i].length));
}

function flattenIntoModel(data, model) {
  for (let i = data.length-1; i >= 0; i--)
    for (let j=0; j < data[i].length; j++)
      model.push(data[i][j]);
}

function nextQuote () {
  this.current = (this.current + 1) % this.quotes.length;
  this.blankOrMore = ' more';
}

export default {
    name: 'SUHC_Quotes',

    data: () => ({
      loading: true,
      loadError: false,
      blankOrMore: '',
      current: -1, // Ugh hacks
      quotes: [],
      nextQuote
    }),

    mounted () { axios({ method: "GET", url: "/data/suhc/quotes.json" })
      .then(result => { /* console.log('Result data', result.data); */
                        shuffleBatches(result.data)
                        flattenIntoModel(result.data, this.quotes);
                        this.loading = false;
                      },
            error  => { console.error(error);
                        this.loading = false;
                        this.loadError = true;
                      })}
  }

</script>
